.team{
    width: 75%;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 2fr 4fr;
    align-items: center;
    margin-bottom: var(--margin);
    align-items: center;
    text-align: center;
}

.team label:first-child{
    font-size: 1.5em;
}


.team img {
    width: 50%;
    margin: auto;
}

.classification{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    align-items: center;
}