/*structure*/
body{
    background: whitesmoke;
}

.app {
    height: 100vh;
    display: grid;
    grid-template-areas:
        'nav nav'
        'aside main';
    grid-template-columns: 1fr 8fr;
    grid-template-rows: min-content;
}

nav {
    width: 100%;
    display: flex;
    margin-bottom: 2.5vh;
    justify-content: space-between;
    align-items: center;
    background: white;
    grid-area: nav;
    box-shadow: 0 2.8px 2.2px var(--primary),
        0 6.7px 5.3px rgba(0, 0, 0, .048),
        0 12.5px 10px rgba(0, 0, 0, .06);
    z-index: 1;
}

nav * {
    color: var(--primary);
    margin: var(--margin-small);
}

nav  a {
    text-decoration: none;
}

aside {
    display: flex;
    flex-direction: column;
    grid-area: aside;
    padding: var(--padding);
    background: var(--primary);
}

main {
    display: flex;
    flex-direction: column;
}

.actions {
    display: flex;
    align-items: center;
}

.actions>* {
    cursor: pointer;
}

.header {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: var(--padding);
}

.header>* {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.container-title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    background: white;
    padding: var(--padding);
    margin-bottom: var(--margin-large);
    border-radius: var(--radius);
}

.container>* {
    margin: 0px var(--margin);
}



.vertical {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: flex-start;
}

.center {
    align-items: center;
}

.horizontal {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: center;
}

.item {
    margin: var(--margin);
}

.clickable {
    cursor: pointer;
}

/*tags*/
.small {
    color: gray;
}

.text-detail {
    font-size: var(--font-small) !important;
}

/*images*/
.logo {
    width: var(--logo);
    object-fit: contain;
}

.icon {
    width: var(--icon);
    height: var(--icon);
}

.logo,
.icon {
    object-fit: contain;
}

nav .icon {
    border: var(--border) solid var(--primary);
}

svg {
    width: var(--action-icon);
    height: var(--action-icon) !important;
}

actions>*,
.icon,
svg {
    transition: all 0.1s ease;
}

.actions>*:hover,
.icon:hover,
svg:hover {
    transform: scale(1.2, 1.2);
}

/*forms*/

form {
    display: flex;
    flex-direction: column;
}

input[type=text],
input[type=number],
input[type=email] {
    margin: var(--margin) 0px;
    padding: var(--padding);
    border: var(--border) solid var(--primary);
    border-radius: var(--radius);
}

input[type="radio"] {
    width: 25px;
    height: 25px;
    background-color: white;
}

input[type="radio"]:after {
    width: 25px;
    height: 25px;
    border-radius: var(--radius);
    position: relative;
    background-color: whitesmoke;
    content: "";
    display: inline-block;
    visibility: visible;
    border: 1px solid gray;
}

input[type="radio"]:checked:after {
    width: 25px;
    height: 25px;
    border-radius: var(--radius);
    background-color: var(--secondary);
    content: "";
    display: inline-block;
    visibility: visible;
}

form>button {
    width: 100%;
    background: var(--primary);
    color: white;
    border: var(--border) solid var(--primary);
    border-radius: var(--radius);
    padding: var(--padding);
}

form>button:hover {
    background: var(--secondary);
}

.rounded {
    border-radius: 50%;
}

button {
    background: var(--primary);
    color: white;
    border: var(--border) solid var(--primary);
    border-radius: var(--radius);
    padding: var(--padding);
}

.button:hover {
    background: var(--secondary);
}

.selectable, .selected{
padding: var(--padding);
}

.selected {
    background: var(--secondary);
    border-bottom: #fff solid var(--border);
    border-radius: var(--radius);
    border: 1px solid whitesmoke;
    color: white;
}


.selected:hover {
    background: var(--primary);
    color: white;
}

.card {

    border-radius: var(--radius);
    background: white;
    box-shadow: 0 2.8px 2.2px var(--primary),
        0 6.7px 5.3px rgba(0, 0, 0, .048),
        0 12.5px 10px rgba(0, 0, 0, .06);
}

/*lists*/

.list {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.list-item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--padding) 0px;
    margin: var(--margin) 0px;
}

.list-item * {
    margin: var(--margin-small);
}

ul {
    list-style: square;
    padding: var(--padding);
}

ul>li {
    margin: var(--margin) 0px;
}

/*grid*/

.grid-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: var(--padding);
}

.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: var(--margin);
    grid-auto-rows: minmax(200px, auto);
    grid-auto-flow: dense;
}

.grid-small {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    grid-gap: var(--margin-large);
    grid-auto-flow: dense;
}

.grid-small>* {
    padding: var(--padding) 0px;
}

.grid-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: white;
    padding: var(--padding);
    margin: var(--margin);
    border-radius: var(--radius);
    box-shadow: 0 2.8px 2.2px var(--primary),
        0 6.7px 5.3px rgba(0, 0, 0, .048),
        0 12.5px 10px rgba(0, 0, 0, .06);
}

.grid-item * {
    text-align: center;
    margin: var(--margin-small);
}

/*messages*/
.warning {
    background: var(--warning);
    color: white;
    padding: var(--padding);
    border-radius: var(--radius);
    margin: var(--margin);
}

/*mediaqueries*/

@media print {
    .app {
        display: flex;
        flex-direction: column;
    }

    nav,
    aside,
    section,
    .actions>* {
        display: none;
    }
}

@media screen and (max-width: 600px) {

    .app,
    .grid-2 {
        display: flex;
        flex-direction: column;
    }


}

.list-bottom{
    margin-top: 50px;
}