.match{
    display:grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: var(--margin);
}

.sets, .points {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
}

.sets input {
    text-align: center;
    margin: var(--margin-small);
}

.sets h3 {
    margin: var(--margin) 0px;
}

.sets input {
    margin: var(--margin-small);
}

.sets button {
    margin-top: var(--margin);
    width: fit-content;

}

.buttons-send{
    display: flex;
    gap: var(--margin);
}

.points {
    display:grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--margin);
}

@media  (max-width: 768px) {
    .match, .points{
        display: flex;
        flex-direction: column;
    }
}